<template>
	<div class="infoarticlewrap">
		<h2 class="infoarticletitle">如果你越来越沉默，说明你越来越成熟</h2>
        <div class="statisticswrap">
            <span>发布时间：2019-5-20</span>
            <span id="busuanzi_container_page_pv">浏览：<span id="busuanzi_value_page_pv">0</span></span>
        </div>
		<div class="infoarticlecontent">
            很多人，总要经历从口若悬河到沉吟不语的过程，不知从什么时候开始，变得越来越沉默，不想去争执，不想去辩论。

            沉默的人并非冷漠

            沉默的人爱思考，对事总会三思，对人总会恭敬，除开先天因素的影响，越来越沉默的人想必是竿头日上的人，阅人多矣的人，他们尊敬白叟，宽带爱人，爱孩子，内心热诚。


            沉默的人重情重义

            即便没有博览群书，也有阅人无数，看不起花言巧语之人，但也绝不辜负肝胆相照之士，宁愿为朋友多行多做，也不愿抱怨昔日己过。
            <img src="https://upload-images.jianshu.io/upload_images/16263448-2c7d494d3f55fd5a?imageMogr2/auto-orient/strip%7CimageView2/2/w/600/format/webp" />

            沉默的人对学习孜孜不倦

            越是知识渊博的人越是深藏若虚，越是短见薄识的人越喜欢彰显自己，当然我觉得彰显自己并不是坏事，很多时候有很多的机会，我们都需要站出来表现自我，提升自我，但是如果是为了一己私利，不负责任的与人传播不切实际的言论，自然会被厌弃，我尊重那些沉默的人，他们在不断的思索生命的意义，越充实越坚持。


            沉默不代表“懦弱”

            生活中，总会有思绪纷扰的时候，总会有言行相悖的时候，而沉默可以让自己保持冷静，这不是懦弱，更不是妥协，而是积累下一次奋起的力量，释放出坚定不移的决心。

            从某种程度来讲，沉默是最好的“为人处事”之道

            随着网络的迅速发展，传播信息的速度越来越快，接收的信息量自然也是不可胜计，但是接收的信息当中似是而非的有很多，并非不易之论，事态的发展都是由很多客官和主管因素影响的，在未经证实前，智者都会选择沉默，生活中更是如此。

            敬给喜欢沉默的你，愿你坚强如刚，愿你悠然自得。
        </div>
	</div>
</template>
